@import url(https://fonts.googleapis.com/css2?family=Anton&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #5cdb7e;
  color: #fff;
  font-family: 'Anton', sans-serif;
  letter-spacing: 1.2px;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{
  margin:0;
  padding:0;
  box-sizing: border-box;
}

.App {
  text-align: center;
  display: flex;
  flex-direction: column;
}

.center {
  padding: 1em;
  position: absolute;
  top: 50%;
  left: 50%;
  margin-right: -50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%)

}

#title {
  text-align: center;
  font-size: 3rem;
  letter-spacing: 5px;
  font-weight: bold;
  -webkit-transform: translateY(-100%);
          transform: translateY(-100%);
}
#start {
  background-color:#47d147 ;
}
p {
  font-size: 1.5rem;
  text-align: center;
}
span {
  font-size: 1.5rem;
}

ul {
  padding: 0;
}
ul li {
  list-style-type: none;
padding: 5px 0;
}

ul li button:focus{
  border-color: #ff6830;
  border-width: 6px;
}
ul li button:focus:hover{
  background-color: peru;
  color: #fff;
}

.textLeft {
text-align:start;
font-weight: bold;
font-size:1.8rem;

}

.row {
  display: flex;
  flex-direction: column;
  align-items: center;
}
#text {
position: absolute;
top: -100%;
left:-10%;
width: 70vh;
  z-index: 0;
}
#writtenText {
  color: blue;
  top: -100%;
  left:-10%;
  width: 70vh;

  position: absolute;
  z-index: 1;
}

.mistake {
  -webkit-transform: translateY(150%);
          transform: translateY(150%);
}
button {
  margin: 5px 0;
  min-width: 25%;
  font-size: 2rem;
  padding: 2px 15px;
  border-radius: 10px;
  border-width: 3px;
  background-color: peru;
  color: #fff;
  transition: 0.5s;
  z-index: 1000;
}
button:hover{
  background-color: #a4fc28;
  color: #48486a;
  transition: 0.5s;
  border-color: #a4fc28
}

input[type=text], select {
  width: 100%;
  padding: 12px 20px;
  margin: 8px 0;
  display: inline-block;
  border: 1px solid transparent;
  background-color: transparent;
  border-radius: 4px;
  font-size: 2rem;
  box-sizing: border-box;
  color: transparent;
  text-align: center;
  outline: transparent;
  cursor: pointer;
}
::-webkit-input-placeholder { 
  color: peru;
  font-size: 2rem;
  text-align: center;
}

.red {
  color: rgb(231, 21, 21);
  font-weight: bold;
}

